import React, { useState, useEffect } from 'react';
import { CacheProvider } from '@emotion/react';
import createCache from '@emotion/cache';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { useLanguage } from './context/LanguageContext';
import Navbar from './components/Navbar';
import SearchBox from './components/SearchBox';
import ProductGrid from './components/ProductGrid';
import Footer from './components/Footer';
import Box from '@mui/material/Box';
import { create } from 'jss';
import rtl from 'jss-rtl';
import { StylesProvider, jssPreset } from '@mui/styles';
import { lightThemeColors, darkThemeColors } from './theme';
import { searchProducts } from './api.js'; 
import ErrorComponent from './components/ErrorComponent';
import ReactGA from 'react-ga4';

ReactGA.initialize('G-BBHP6VP9NT');

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });

function App() {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [onSale, setOnSale] = useState(false);

  ReactGA.send({ hitType: "pageview", page: window.location.pathname });

  const toggleOnSale = () => {
    setOnSale(!onSale);
  };

  const loadThemePreference = () => {
    const savedTheme = localStorage.getItem('themePreference');
    return savedTheme ? savedTheme === 'dark' : false;
  };
  const [darkMode, setDarkMode] = useState(loadThemePreference());

  const { isRtl } = useLanguage();

  useEffect(() => {
    document.documentElement.dir = isRtl ? 'rtl' : 'ltr';
  }, [isRtl]);

  const rtlCache = createCache({
    key: 'muirtl',
    prepend: true,
    stylisPlugins: isRtl ? [require('stylis-plugin-rtl').default] : [],
  });

  const theme = React.useMemo(() => createTheme({
    palette: darkMode ? darkThemeColors.palette : lightThemeColors.palette,
  }), [darkMode]);

  const toggleDarkMode = () => {
    const newTheme = !darkMode;
    setDarkMode(newTheme);
    localStorage.setItem('themePreference', newTheme ? 'dark' : 'light');
  };

  const handleSearch = async (searchTerm) => {
    setLoading(true);
    setError('');
    try {
      const searchResults = await searchProducts(searchTerm); // Use the searchProducts function
      setProducts(searchResults.map(product => ({ ...product, likeStatus: null }))); // Reset likeStatus
    } catch (e) {
      setError('Failed to fetch products. Please try again later.');
      setProducts([]);
    }
    setLoading(false);
  };

  const handleDislike = (productId) => {
    setProducts(products.filter(product => product.productId !== productId));
  };

  return (
    <CacheProvider value={rtlCache}>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <Box display="flex" flexDirection="column" minHeight="100vh">
            <Navbar
              toggleTheme={toggleDarkMode}
              isDarkMode={darkMode}
              onSale={onSale}
              onSaleToggle={toggleOnSale}
            />
            <Box flexGrow={1}>
            <SearchBox onSearch={handleSearch} loading={loading} />
              {error && <ErrorComponent message={error} />} 
              {!loading && !error && (
                <ProductGrid
                  products={products}
                  onProductDislike={handleDislike}
                  onSale={onSale}
                />
              )}
            </Box>
            <Footer />
          </Box>
        </ThemeProvider>
      </StylesProvider>
    </CacheProvider>
  );
}

export default App;