import React, { useState } from 'react';
import {
  Card, CardActionArea, CardActions, CardContent,
  CardMedia, Typography, IconButton, Button, Box
} from '@mui/material';import CircularProgress from '@mui/material/CircularProgress';
import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import ThumbDownIcon from "@mui/icons-material/ThumbDown";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { useLanguage } from '../context/LanguageContext';
import { useTheme } from '@mui/material/styles';
import { sendLikeDislikeToGA, sendShopVisitToGA } from './analytics.js';

function formatPrice(price) {
  return new Intl.NumberFormat('fa-IR').format(price);
}

export default function ProductCard({ product, onDislike }) {
  const [imageLoaded, setImageLoaded] = React.useState(false);
  const { language } = useLanguage();
  const theme = useTheme();
  const [likeStatus, setLikeStatus] = useState('default'); 

  const handleLike = () => {
    sendLikeDislikeToGA(product.linkSrc, 'like'); // Analytics call
    setLikeStatus(likeStatus === 'liked' ? 'default' : 'liked');
  };

  const handleDislike = () => {
    sendLikeDislikeToGA(product.linkSrc, 'dislike'); // Analytics call
    setLikeStatus(likeStatus === 'disliked' ? 'default' : 'disliked');
    onDislike(product.productId);
  };

  const handleShopVisit = () => {
    sendShopVisitToGA(product.linkSrc); // Analytics call
    window.location.href = product.linkSrc;
  };

  const localization = {
    en: {
      like: 'Like',
      dislike: 'Dislike',
      shop: 'Shop',
    },
    fa: {
      like: '',
      dislike: '',
      shop: '',
    },
  };
  
  if (likeStatus === 'disliked') {
    return null;
  }

  return (
    <Card 
      sx={{ 
        maxWidth: 345, 
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        maxHeight: 450,
        m: 2, 
        bgcolor: theme.palette.background.paper, 
        color: theme.palette.text.primary, 
        boxShadow: theme.shadows[1], 
        "&:hover": {
          transform: 'scale(1.03)', 
          transition: 'transform 0.3s ease-in-out',
          boxShadow: theme.shadows[10], 
        },
      }}
    >
      <CardActionArea sx={{ flexGrow: 1 }}>
      <a 
        href={product.linkSrc} 
        target="_blank" 
        rel="noopener noreferrer" 
        onClick={(e) => {
          e.preventDefault(); 
          handleShopVisit();
        }}
        style={{ display: 'block' }}
      >
        <CardMedia
          component="img"
          image={product.imageSrc}
          alt={product.name}
          style={{ aspectRatio: '4 / 3' }}
          onLoad={() => setImageLoaded(true)}
          onError={() => setImageLoaded(false)}
          sx={{
            boxShadow: 1,
            transition: 'box-shadow 0.2s ease-in-out',
            '&:hover': {
              boxShadow: 6, 
            },
            height: 200,
            objectFit: 'contain',
            p: 2, 
            bgcolor: "background.default",
          }}
        />
        {
          !imageLoaded && (
            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 140 }}>
              <CircularProgress />
            </div>
          )
        }
      </a>
        <CardContent dir="rtl">
          <Typography gutterBottom variant="h6" component="div" noWrap>
              {product.name}
            </Typography>
              <Typography variant="body1" component="div" sx={{ pt: 1, pb: 1 }}>
                <Box component="span" sx={{ marginRight: '8px' }}>
                  {formatPrice(product.currentPrice)} 
                </Box>
                {product.oldPrice && (
                  <Box component="span" sx={{ textDecoration: 'line-through', color: 'text.secondary', marginRight: '8px' }}>
                    {formatPrice(product.oldPrice)} 
                  </Box>
                )}
              </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions sx={{ justifyContent: 'space-between', padding: 1.5 }}>
        
        <IconButton
          aria-label="like"
          size="large"
          onClick={handleLike}
          sx={{
            color: likeStatus === 'liked' ? 'green' : likeStatus === 'default' ? 'gold' : 'inherit',
            transform: likeStatus === 'liked' ? 'scale(1.2)' : 'none',
            transition: 'transform 0.3s, color 0.3s',
          }}
        >
          <ThumbUpIcon />
        </IconButton>
        <IconButton
          aria-label="dislike"
          size="large"
          onClick={handleDislike}
          sx={{
            color: likeStatus === 'disliked' ? 'red' : likeStatus === 'default' ? 'red' : 'red',
            transform: likeStatus === 'disliked' ? 'scale(1.2)' : 'none',
            transition: 'transform 0.3s, color 0.3s',
          }}
        >
          <ThumbDownIcon />
        </IconButton>
          <Button variant="contained" startIcon={<ShoppingCartIcon />} onClick={handleShopVisit}>
            {localization[language].shop}
          </Button>
      </CardActions>
    </Card>
  );
}