import React from 'react';
import { useLanguage } from '../context/LanguageContext';

const examples = {
  en: [
    'Men\'s Shirts',
    'Women\'s Shirts',
    'Women\'s Coats',
    'Women\'s Raincoats',
    'Long Shirt Manteau',
    'Mom Fit Jeans',
    'Women\'s Manteau',
  ],
  fa: [
    ' ',
    ' ',
    ' ',
    ' ',
    ' ',
    '  ',
    ' ',
  ]
};

const ExamplesComponents = ({ isActive, onExampleClick }) => {
  const { language } = useLanguage();
  
  if (isActive) return null; // Hide examples if search has started

  return (
    <div style={{ textAlign: 'center', marginTop: '20px' }}>
      <h4>
        {language === 'en' ? 'Popular Searches' : ' '}
      </h4>
      {examples[language].map((example, index) => (
        <button
          key={index}
          onClick={() => onExampleClick(example)}
          style={{
            background: 'gold',
            border: 'none',
            borderRadius: '20px',
            padding: '10px 15px',
            color: 'black',
            margin: '0 5px',
            cursor: 'pointer',
            display: 'inline-block', // Adds inline style, remove if you have good CSS handling
            marginBottom: '10px' // Space between rows of buttons if wrapping occurs
          }}
        >
          {example}
        </button>
      ))}
    </div>
  );
};

export default ExamplesComponents;