import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import { useLanguage } from '../context/LanguageContext';
import CircularProgress from '@mui/material/CircularProgress';
import { sendSearchToGA } from './analytics.js';
import TitleComponents from './TitleComponents';
import ExamplesComponents from './ExamplesComponents';

export default function SearchBox({ onSearch, loading }) {
  const [searchTerm, setSearchTerm] = useState('');
  const [isActive, setIsActive] = useState(false);
  const { language } = useLanguage();

  const localization = {
    en: {
      searchPlaceholder: 'Search Products',
      searchButton: 'Search',
    },
    fa: {
      searchPlaceholder: ' ',
      searchButton: '',
    },
  };
  
  const handleInputChange = (event) => {
    setSearchTerm(event.target.value);
    if (event.target.value === "") {
      setIsActive(false); 
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setIsActive(true);
    onSearch(searchTerm);
    sendSearchToGA(searchTerm);
  };
  
  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: isActive ? '20vh' : '80vh', // Adjust height based on active state
        padding: '20px',
        transition: 'height 1s ease', // Animate the transition of the height
      }}
      noValidate
      autoComplete="off"
      className={`searchBox ${isActive ? 'searchBoxActive' : ''}`}
      >
      <TitleComponents isActive={isActive} />
      <TextField
        id="search-input"
        placeholder={localization[language].searchPlaceholder}
        InputLabelProps={{
          shrink: true,
          className: language === 'fa' ? 'rtl-label' : '',
        }}
        variant="outlined"
        value={searchTerm}
        onChange={handleInputChange}
        sx={{
          width: '100%',
          maxWidth: {
            sm: '540px',
            md: '720px',
            lg: '960px',
          },
          mb: 2 
        }}
      />
      <Button
        variant="contained"
        color="primary"
        type="submit"
        sx={{
          width: { sm: 'auto' },
          maxWidth: { sm: '540px', md: '720px', lg: '960px' },
        }}
      >
        {localization[language].searchButton}
      </Button>
      <ExamplesComponents isActive={isActive} onExampleClick={setSearchTerm}/>

      {loading && (
        <CircularProgress
          size={68} // Size of the spinner
          sx={{
            position: 'absolute', // Position it in the center of the search box
            top: '50%',
            left: '50%',
            marginTop: '-34px', // Half the size of the spinner to center it
            marginLeft: '-34px',
          }}
        />
      )}
    </Box>
  );
}