import ReactGA from 'react-ga4';

export const sendSearchToGA = (searchTerm) => {
  ReactGA.event({
    category: 'Search',
    action: 'User searched',
    label: searchTerm
  });
};

export const sendLikeDislikeToGA = (productUrl, action) => { // `action` can be 'like' or 'dislike'
  ReactGA.event({
    category: 'Product Card',
    action: `User ${action}d a product`,
    label: productUrl
  });
};

export const sendShopVisitToGA = (productUrl) => {
  ReactGA.event({
    category: 'Shop Visit',
    action: 'User visited a shop',
    label: productUrl
  });
};