import React from 'react';
import { useLanguage } from '../context/LanguageContext';

const TitleComponents = ({ isActive }) => {
    const { language } = useLanguage();

    const titleStyle = {
        textAlign: 'center',
        display: isActive ? 'none' : 'block'
    };
    const subtitleStyle = {
        textAlign: 'center',
        display: isActive ? 'none' : 'block'
    };

  return (
    <>
    <h2 style={titleStyle}>
        {language === 'en' ? 'Find any dress or accessory right now!' : '          !'}
    </h2>
    <h4 style={subtitleStyle}>
        {language === 'en' ? 'Search among thousands of products and stores' : '     '}
    </h4>
    </>
  );
};

export default TitleComponents;