import * as React from 'react';
import { AppBar, Toolbar, Typography, IconButton, Switch, FormControlLabel } from '@mui/material';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import LightModeIcon from '@mui/icons-material/LightMode';
import { useTheme } from '@mui/material/styles';
import { useLanguage } from '../context/LanguageContext';
import { Button } from '@mui/material';


const Navbar = ({ toggleTheme, isDarkMode, onSale, onSaleToggle }) => {
  const themeIcon = isDarkMode ? <LightModeIcon /> : <DarkModeIcon />;
  const { language, toggleLanguage, textDirection } = useLanguage();
  const theme = useTheme();

  const logoText = {
    en: 'ShikSearch',
    fa: ' '
  };

  const onSaleLabel = {
    en: 'On Sale',
    fa: ' ',
  };

  return (
    <AppBar position="static" color="default" sx={{ direction: textDirection }}>
      <Toolbar sx={{ display: 'flex', justifyContent: 'space-between', backgroundColor: (theme) => theme.palette.background.paper }}>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1, textAlign: 'center' }}>
          {logoText[language]}
        </Typography>
        {/* Position the toggle language button to the left or right consistently */}
        <div style={{ position: 'absolute', left: 16 }}> {/* Example for left-aligned */}
          <Button onClick={toggleLanguage} variant="outlined" color="inherit">
            {language === 'en' ? 'FA' : 'EN'}
          </Button>
        </div>

        {/* Place the theme toggle button consistently on the opposite side */}
        <div style={{ position: 'absolute', right: 16 }}> {/* Example for right-aligned */}
        <FormControlLabel
          control={<Switch checked={onSale} onChange={onSaleToggle} />}
          label={onSaleLabel[language]}
          style={{ marginRight: theme.spacing(2) }}
          labelPlacement="start" 
        />
          <IconButton
            size="large"
            edge="end"
            color="inherit"
            aria-label="toggle theme"
            onClick={toggleTheme}
          >
            {themeIcon}
          </IconButton>
        </div>
      </Toolbar>
    </AppBar>
  );
}

export default Navbar;