import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import ProductCard from './ProductCard';

function ProductGrid({ products, onProductDislike, onSale }) {
  const onSaleProducts = onSale
  ? products.filter(product => product.oldPrice && product.currentPrice < product.oldPrice)
  : products;

  return (
    <Container maxWidth="lg"> {/* Adjust maxWidth as needed */}
      <Grid
        container
        spacing={2} // Change spacing for desired effect
        justifyContent="center"
        alignItems="flex-start" // or "center" depending on desired vertical alignment
      >
        {onSaleProducts.map(product => (
          <Grid item key={product.productId} xs={12} sm={6} md={4} lg={3}>
            <ProductCard product={product} onDislike={onProductDislike} />
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default ProductGrid;
